import React, { Component } from 'react'
import './Home.css';
import { BrowserRouter as Router, Link, Route, Switch, useParams } from 'react-router-dom';
// import Moh from '../../pages/Moh/Moh';
// import Moi from '../..pages/Moi/Moi';
import Navbar from '../Navbar';
import Services from '../../pages/Services'
// import Moh from '../pages/Moh/Moh';
import { MobileStepper, Modal } from '@material-ui/core';
import Min from '../../pages/Min'

export class Home extends Component {
    constructor (props){
        super(props);
        this.state = {
            items: [],
            isLoaded: false,
        };
    }

    async componentDidMount(){
        fetch('https://dry-springs-97013.herokuapp.com/')
        .then(res => res.json())
        .then(json => {
            this.setState({
                isLoaded: true,
                items: json,
            })
        });
    }

    render() {
        // renders the ministries content 
        var { isLoaded, items } = this.state;
        if(!isLoaded){
            return <div>Loading...</div>
        }

        else {

            return (
                
                <div className="App">
                    <div className="container"> 
                    
                        <ul className="list">
                            {items.map(item => (
                                <li key={item.id}>
                                <span>{item.id}</span>
                                    {item.name}
                                </li>   
                                
                            ))}; 
                        </ul>
                        
                    
                    </div>
                    <footer>&copy; Smart Zambia Institute {(new Date()).getFullYear()}</footer>
                </div>                
            )
        }
        
    }
}

export default Home
